import React, { useState } from "react";
import Axios from "axios";

const AddCommentForm = ({ name, setArticleInfo }) => {
  const [username, setUsername] = useState("");
  const [commentText, setCommentText] = useState("");
  const addComment = async () => {
    const res = await Axios({
      method: "post",
      url: `/api/articles/${name}/add-comment`,
      data: {
        username,
        text: commentText,
      },
      headers: { "Content-Type": "application/json" },
    });
    const body = res.data;
    setArticleInfo(body);
    setUsername("");
    setCommentText("");
  };
  const addCommentIf = (event) => {
    event.preventDefault();
    if (username.length !== 0 && commentText.length !== 0) {
      addComment();
    }
  };
  return (
    <div id="add-comment-form">
      <h3>Add a comment: </h3>
      <form>
        <label>
          Name:
          <input
            type="text"
            value={username}
            onChange={(event) => {
              event.preventDefault();
              setUsername(event.target.value);
            }}
            required
          />
        </label>
        <label>
          Comment:
          <textarea
            rows="4"
            cols="50"
            value={commentText}
            onChange={(event) => {
              event.preventDefault();
              setCommentText(event.target.value);
            }}
            required
          />
        </label>

        <button onClick={(event) => addCommentIf(event)}>Add Comment</button>
      </form>
    </div>
  );
};

export default AddCommentForm;
